  .login_li{
    margin-top:8%;
    position:relative;
    left:12%;
    width:70%;
    height:40%;
  }
  
  .login_li img{
    position: absolute;
    left:-10%;
    top:2%;
    width:20%;
  }
  
  #login_form2{
    position:absolute;
    left:8%;
    top:55%;
    width:84%;
    font-size:120%;
