  #login_logo{
    position:absolute;
    margin-top:0;
    padding-left:0;
    width:100%;
    height:40%;
  }
  
  #login_logo>li{
    display:inline;
    margin-left:8%;
    margin-right:10%;
  }
  
  #login_logo>li:last-child{
    margin-right:0;
  }
  
  #login_logo img{
    width:15%;
  }
  
  #camera_div{
    margin-top:13%;
    width:105px;
    height:105px;
    border:solid 1px #a8a8a8;
    border-radius:105px;
